import {
	classNames,
	INVALID,
	mpsToKph,
	mpsToMph,
	rpsToRpm,
	// showDebugMessageSmall,
	widgetSettings
} from './../../lib/utils';
import {
	IWidgetSetting,
	lowPerformanceMode,
	highPerformanceMode,
	showAllMode
} from '../app/app';
import { action, observable } from 'mobx';
import { EEngineType } from './../../types/r3eTypes';
import { observer } from 'mobx-react';
import r3e, {
	registerUpdate,
	unregisterUpdate,
	nowCheck
} from './../../lib/r3e';
import React from 'react';
import style from './motec.scss';

interface IProps extends React.HTMLAttributes<HTMLDivElement> {
	settings: IWidgetSetting;
}

@observer
export default class Motec extends React.Component<IProps, {}> {
	@observable
	speed = INVALID;

	@observable
	rpm = 0;

	@observable
	maxRpm = 0;

	@observable
	upshiftRps = 0;

	@observable
	gear = 0;

	@observable
	limiter = 0;

	gearNameLookup = {};

	@observable
	lastCheck = 0;

	@observable
	isPlayer = false;

	@observable
	playerSlotId = -1;

	@observable
	userId = -1;

	@observable
	lastBlinkTime = -1;

	@observable
	gearColor = 'white';

	constructor(props: IProps) {
		super(props);

		registerUpdate(this.update);

		const isElectric =
			r3e.data.VehicleInfo.EngineType === EEngineType.Electric;

		this.gearNameLookup = isElectric
			? {
					'-1': 'R',
					0: 'N',
					1: 'D',
					2: 'S'
			  }
			: {
					'-1': 'R',
					0: 'N'
			  };
	}

	componentWillUnmount() {
		unregisterUpdate(this.update);
	}

	@action
	private update = () => {
		if (
			r3e.data.PitLimiter > 0 &&
			this.props.settings.subSettings.plBlink.enabled
		) {
			const diff = nowCheck - this.lastBlinkTime;
			if (
				diff > 250 &&
				diff < 500
			) {
				this.gearColor = 'red';
			}
			if (
				diff > 500
			) {
				this.gearColor = 'blue';
				this.lastBlinkTime = nowCheck;
			}
		} else {
			this.gearColor = this.rpm > this.maxRpm * 0.97
				?	'#fe0000'
				:	this.rpm > this.upshiftRps * 0.92
					?	'lime'
					:	'white';
			this.lastBlinkTime = nowCheck;
		}

		if (
			(
				highPerformanceMode &&
				nowCheck - this.lastCheck >= 16
			) ||
			(
				lowPerformanceMode &&
				nowCheck - this.lastCheck >= 66
			) ||
			(
				!lowPerformanceMode &&
				!highPerformanceMode &&
				nowCheck - this.lastCheck >= 33
			)
		) {
			if (this.playerSlotId === -1 && r3e.data.SteerLockDegrees > 0) {
				this.playerSlotId = r3e.data.VehicleInfo.SlotId;
				this.userId = r3e.data.VehicleInfo.UserId;
			}
			this.isPlayer = this.playerSlotId !== -1 &&
				r3e.data.VehicleInfo.SlotId === this.playerSlotId;
			this.lastCheck = nowCheck;
			this.speed = r3e.data.CarSpeed;
			this.rpm = rpsToRpm(r3e.data.EngineRps);
			this.maxRpm = rpsToRpm(r3e.data.MaxEngineRps);
			this.upshiftRps = rpsToRpm(r3e.data.UpshiftRps);
			this.gear = r3e.data.Gear;
			this.limiter = r3e.data.PitLimiter;
		}
	};

	render() {
		return (
			<div
				{...widgetSettings(this.props)}
				className={classNames(style.motec, this.props.className, {
					shouldShow: this.speed !== INVALID,
					plBlink:
						this.props.settings.subSettings.plBlink.enabled,
					plEnabled: showAllMode || this.limiter === 1
				})}
			>
				{/* Speed*/}
				<div
					className="speed mono"
					style={{
						color: this.limiter
							?	this.gearColor
							:	'white'
					}}
				>
					{showAllMode
						?	65
						:	this.props.settings.subSettings.showMPH.enabled
							? mpsToMph(this.speed).toFixed(0)
							: mpsToKph(this.speed).toFixed(0)}
				</div>

				{/* RPM */}
				<div className="rpm">
					<div
						className="rpmBar"
						style={{
							width: `${showAllMode
								?	50
								:	(this.rpm / this.maxRpm) * 100}%`,
							background: this.gearColor
						}}
					/>
				</div>

				{/* Gear */}
				<div
					className="gear mono"
					style={{
						color: this.rpm > this.maxRpm * 0.97
							?	'#fe0000'
							:	this.rpm > this.upshiftRps * 0.92
								?	'lime'
								:	'white'
					}}
				>
					{showAllMode
						?	2
						:	this.gearNameLookup[this.gear] || this.gear}
				</div>
			</div>
		);
	}
}
