import {
	classNames,
	widgetSettings,
	INVALID
} from './../../lib/utils';
import {
	IWidgetSetting,
	showAllMode
} from '../app/app';
import { action, observable } from 'mobx';
import { eFormationLap } from '../manualStart/manualStart';
import { ESession } from './../../types/r3eTypes';
import { observer } from 'mobx-react';
import { times } from 'lodash-es';
import r3e, { registerUpdate, unregisterUpdate, nowCheck } from '../../lib/r3e';
import React from 'react';
import style from './startingLights.scss';

interface IProps extends React.HTMLAttributes<HTMLDivElement> {
	settings: IWidgetSetting;
}

@observer
export default class StartingLights extends React.Component<IProps, {}> {
	@observable
	startLights = INVALID;

	@observable
	lightCount = 5;
	@observable
	goneGreen = 1;
	@observable
	wentGreen = 1;
	@observable
	nowGreen = 0;
	@observable
	isRace = false;
	@observable
	lastCheck = 0;
	@observable
	manualFormation = eFormationLap || -1;

	constructor(props: IProps) {
		super(props);

		registerUpdate(this.update);
	}

	componentWillUnmount() {
		unregisterUpdate(this.update);
	}

	@action
	private update = () => {
		if (
			nowCheck - this.lastCheck >= 0
		) {
			this.lastCheck = nowCheck;
			this.isRace = r3e.data.SessionType === ESession.Race;
			this.manualFormation = eFormationLap;
			if (this.manualFormation === -1) {
				this.startLights = r3e.data.StartLights;
				if (this.startLights < 6) {
					this.goneGreen = 0;
					this.wentGreen = 0;
					this.nowGreen = 0;
				}
				if (this.startLights >= 6 && this.goneGreen === 0) {
					this.goneGreen = 1;
					this.nowGreen = nowCheck;
				}
				if (
					this.goneGreen > 0 &&
					this.wentGreen === 0 &&
					(nowCheck - this.nowGreen) >= 3000
					) {
						this.wentGreen = 1;
				}
			}
		}
	}

	render() {
		if (
			(
				!this.isRace &&
				!showAllMode
			) ||
			(
				this.isRace &&
				this.startLights >= 6 &&
				this.wentGreen === 1
			) ||
			this.manualFormation !== -1
		) {
			return null;
		}

		return (
			<div
				className={classNames(
					style.startingLights,
					this.props.className
				)}
			>
				<div
					className="inner"
					{...widgetSettings(this.props)}
				>
					{times(this.lightCount).map((i) => {
						return (
							<div
								key={`light-${i}`}
								className={classNames('light', {
									active: showAllMode
										?	true
										:	this.startLights > i,
									green: showAllMode
										?	false
										:	this.startLights > this.lightCount
								})}
							/>
						);
					})}
				</div>
			</div>
		);
	}
}
